import { YMaps, Map, SearchControl } from '@pbe/react-yandex-maps';

const MapContainer = () => {
    return (
        <div>
            <YMaps >
                <Map 
                    defaultState={{ center: [62.02, 129.715], zoom: 14 }} 
                    width="100%"
                    height="100vh"
                />
                <SearchControl options={{ float: "right" }} />
            </YMaps>
        </div>

    )
};

export default MapContainer;