import React from 'react'

const styles = {
  search: {
    position: 'absolute',
    left: 37.5,
    top: 12.5,
    width: '250px',
    height: '40px',
    backgroundColor: '#fff',
    border: '2px solid #DBDBDB',
    margin: '0px 10px',
    height: '36px',
    padding: '0px 15px',
    lineHeight: 0,
    borderRadius: 5,
    zIndex: 2,
    outline: "none",
  }
}

const SearchComponent = () => {
  return (
    <div>
      <input 
        style={styles.search}
        type="text"
        name="Поиск"
        placeholder='Поиск'
        autoComplete="off"
      />

    </div>

  );
}

export default SearchComponent;